<template>
  <body>
    <nav class="navbar navbar-dark fixed-top bg-dark">
      <div class="container-fluid">
        <a class="navbar-brand" href="#">Navbar</a>
        <ul class="navbar-nav">
          <li class="nav-item">
            <a class="nav-link active" aria-current="page" href="#">Home</a>
          </li>
          <li class="nav-item">
            <a class="nav-link" href="#">Link</a>
          </li>
          <li class="nav-item dropdown">
            <a class="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
              Dropdown
            </a>
            <ul class="dropdown-menu">
              <li><a class="dropdown-item" href="#">Action</a></li>
              <li><a class="dropdown-item" href="#">Another action</a></li>
              <li>
                <hr class="dropdown-divider">
              </li>
              <li><a class="dropdown-item" href="#">Something else here</a></li>
            </ul>
          </li>
          <li class="nav-item">
            <a class="nav-link disabled">Disabled</a>
          </li>
        </ul>
      </div>

    </nav>
    <div class="container-fluid">
      <div class="row justify-content-md-center mt-5">
        <div class="col-3">
          <div class="card">
            <div class="card-header text-white bg-dark">
              Tasks
            </div>
            <ul class="list-group list-group-flush">
              <!-- Tasks Headers here -->
              <li v-bind:class=get_task_class(task) style="cursor: pointer;" v-for="task in tasks.value" :key="task.id"
                @click="select_task_by_id(task.id)">{{task.id}}</li>
            </ul>
          </div>
        </div>
        <div class="col-9">
          <!-- Tasks cards Here -->
          <div class="container">
            <TaskCard :is="selected_task" :id="selected_task.id" :struct="selected_task.struct"
              :active="selected_task.active" />
          </div>
        </div>
      </div>
    </div>
  </body>
</template>

<script setup>
import { ref, reactive, computed } from "vue";
import TaskCard from '@/components/TaskCard.vue';
//import { useVSUIStore } from '@/stores/vsui_store.js'
import io from 'socket.io-client';
import { useToast } from "vue-toastification";
// INIT
//const store = useVSUIStore
const socket = io({
  extraHeaders: {
    'type': 'web'
  }
})
const toast = useToast();

// REACTIVES
const tasks = reactive({
  value: [{
    "id": "Welcome to VSUI",
    "struct":
      [{ "key": "Welcome to VSUI", "component": "LogElement", "data": ['no data loaded'] }],
    "active": false
  }]
})
// the index of the currently viewed task in the list of tasks
const selected_task_index = ref(0)
// the currently selected task, found based on the selected index
const selected_task = computed(() => (selected_task_index.value < tasks.value.length) ? tasks.value[selected_task_index.value] : tasks.value[0])
// set the selected task to be the one with the chosen id
function select_task_by_id(target_id) {
  selected_task_index.value = tasks.value.findIndex(x => x.id === target_id);
}

// FUNCTIONS
// get the class for the item in the tasks list based on: selection status; running state ect
function get_task_class(task) {
  // spaces must be added before appended class statements
  var class_string = "list-group-item pointer"
  if (selected_task.value.id == task.id) {
    class_string += " border border-primary border-4"
  }
  if (task.active) {
    class_string += " active-task"
  } else {
    class_string += " inactive-task"
  }
  return class_string
}

// SocketIO

// these events handle the connection between the frontend and backend
socket.on('connect', () => {
  console.log('connected')
  toast("connected");
})
socket.on('disconnect', () => {
  console.log('disconnected')
  toast.error('disconnected');
})

socket.on('data_push', (data) => {
  tasks.value = JSON.parse(data.data);
  console.log('data push')
})

// pass notifications to toasts
socket.on('toast', (data) => {
  var txt = data.txt;
  var type = data.type;
  if (type == 'success') {
    toast.success(txt);
  } else if (type == 'error') {
    toast.error(txt);
  } else if (type == 'info') {
    toast.info(txt);
  } else if (type == 'warning') {
    toast.warning(txt);
  }
})
</script>

<style>
.active-task {
  background-color: rgba(211, 211, 211, 0.5);
}

.inactive-task {
  background-color: rgba(211, 211, 211, 0.5);
  opacity: 0.7;
}

body {
  padding-top: 20px;
}
</style>
